Vue.prototype.qsParam = function (data) {
    return Qs.stringify(data);
};

//axios
Vue.prototype.request = function (method, url, data, headers) {
    axios({
        method: method,
        url: url,
        data: this.qsParam(data),
        headers: headers
    }).then(res => {
        console.log(res);
        this.res = res;
    });

    return this.res;
};

Vue.prototype.qsParam = function (data) {
    return Qs.stringify(data);
};



