<template>
    <div class="movie">
        <div>
            <el-form
                    ref="searchForm"
                    :inline="true"
                    :model="searchMap"
                    style="margin-top: 20px;margin-left: 0px">
                <el-form-item prop="cinemaForm.address">
                    <el-input>
                        <template slot="prepend"></template>
                    </el-input>
                </el-form-item>
                <el-button icon="el-icon-search"></el-button>
                <el-button type="primary" plain @click="dialogVisible=true"></el-button>
            </el-form>
        </div>
        <el--container>
            <el-main>
                <el-table
                        v-loading="loading"
                        :data="tableData"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            prop="id"
                            label="id"
                            width="50">
                    </el-table-column>
                    <el-table-column
                            prop="movieInfo.poster"
                            label=""
                            width="180">
                        <template slot-scope="props">
                            <img :src="props.row.movieInfo.poster" alt="" class="poster">
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="movieName"
                            label=""
                            width="100">
                    </el-table-column>
                    <!--todo -->
                    <el-table-column
                            class="expansion"
                            prop="movieInfo.introduce"
                            label=""
                            width="260">
                    </el-table-column>
                    <el-table-column
                            prop="movieInfo.director"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="movieInfo.actors"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="movieInfo.movieLength"
                            label=""
                            width="100px">
                    </el-table-column>
                    <el-table-column
                            prop="movieInfo.release"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column label="" width="200px">
                        <template>
                            <el-button type="success" @click="toDelete"></el-button>
                            <br><br>
                            <el-button type="danger" @click="toDelete"></el-button>
                        </template>
                    </el-table-column>
                </el-table>

            </el-main>

        </el--container>
        <!---->
        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="35%"
                style="height: 800px"
                :before-close="handleClose"
                center="true"
                lock-scroll="false">
            <div>
                <span>:
                    <el-input style="width: 350px"></el-input>
                </span><br>
                <span>:
                    <el-input style="width: 350px"></el-input>
                </span><br>
                <span>:
                    <el-input type="textarea" style="width: 350px"></el-input>
                </span><br>
                <span>&nbsp; &nbsp;&nbsp;&nbsp;:
                    <el-input style="width: 350px"></el-input>
                </span><br>
                <span>:
                    <el-input style="width: 350px"></el-input>
                </span><br>
                <span>:
                    <el-input placeholder="" style="width: 350px"></el-input>
                </span><br>

                <span>:
                    <el-input placeholder="" style="width: 350px"></el-input>
                </span><br>
                <span>:
                    <el-upload
                            class="upload"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-preview="handlePreview"
                            :on-remove="handleRemove"
                            :before-remove="beforeRemove"
                            list-type="picture"
                            :limit="1"
                            :on-exceed="handleExceed"
                            :file-list="fileList">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
                </span>
            </div>
            <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="update"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import * as CommonUrls from '../../api/commonUrls'
    import {AxiosInstance as Axios} from "axios";
    import {request} from "../../utils/request";

    export default {
        name: "movie",
        data() {
            return {
                //
                fileList: [{
                    name: '',
                    url: '',
                }],
                input: '',
                movieForm: {
                    poster: '',
                    foreign: '',
                    movieName: '',
                    introduce: '',
                    director: '',
                    actors: '',
                    movieLength: '',
                    release: '',
                },
                /**/
                dialogVisible: false,
                tableData: [],
                limit: 20, // todo ,
                offset: 0, //todo 
            }
        },
        methods: {
            /**/
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            GetTableData() {
                CommonUrls.getAllMovie({
                    limit: this.limit,
                    offset: this.offset,
                }, 'get').then(res => {
                    return res.json();
                }).then(res => {
                    // if (res.code === 200) {
                    this.tableData = res;
                    // console.log("getAllMovie============");
                    // console.log(res);

                    // } else {
                    //     console.log("")
                    // }
                }).catch(err => {
                    console.log(err)
                })
            },
            /**/
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`1 ${files.length}  ${files.length + fileList.length} `);
            },
            beforeRemove(file, fileList) {
                return this.$confirm(` ${file.name}`);
            }

        },
        mounted() {
            this.GetTableData();
        }
    }
</script>

<style scoped>
    expansion {
        white-space: pre-line;
        width: 10.3rem;
        overflow: hidden;
        -webkit-line-clamp: 4;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }

    .poster {
        width: 180px;
        height: 300px;
    }
</style>