<template>
    <div class="user">
        <div>
            <!--            <el-breadcrumb separator-class="el-icon-arrow-right">-->
            <!--                <el-breadcrumb-item :to="{ path: '/dashboard' }"></el-breadcrumb-item>-->
            <!--                <el-breadcrumb-item></el-breadcrumb-item>-->
            <!--            </el-breadcrumb>-->
        </div>
        <el-container>

            <el-main>
                <div>
                    <el-form
                            ref="searchForm"
                            :inline="true"
                            :model="searchMap"
                            style="margin-top: 20px;margin-left: 0px">
                        <el-form-item prop="companyName">
                            <el-input>
                                <template slot="prepend" prop="userName"></template>
                            </el-input>
                        </el-form-item>
                        <el-button icon="el-icon-search"></el-button>
                    </el-form>
                </div>
                <el-table
                        :data="tableData"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            prop="id"
                            label="id"
                            width="100px">
                    </el-table-column>
                    <el-table-column
                            prop="userName"
                            label=""
                            width="100px">
                    </el-table-column>
                    <el-table-column
                            prop="sex"
                            label=""
                            width="100px">
                    </el-table-column>
                    <el-table-column
                            prop="password"
                            label=""
                            width="150">
                    </el-table-column>
                    <el-table-column
                            prop="phoneNumber"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="createTime"
                            label=""
                    >
                    </el-table-column>
                    <el-table-column
                            prop="modifiedTime"
                            label="">
                    </el-table-column>

                    <el-table-column label="" width="200px">
                        <template slot-scope="scope">
                            <el-button type="success" @click="openGet(scope.row.id)"></el-button>
                            <el-button type="danger" @click="toDelete(scope.row.id)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!--todo -->
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[5,10,20,30]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total"
                ></el-pagination>
                <!---->
                <el-dialog
                        title=""
                        :visible.sync="dialogVisible"
                        width="30%"
                        :before-close="handleClose">
                    <el-form ref="form" :model="tableData" label-width="80px">
                        <el-form-item label="" prop="userName">
                            <el-input type="text" v-model="userForm.userName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input v-model="userForm.password"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input v-model="userForm.password"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="sex">
                            <el-radio v-model="userForm.sex" label="1"></el-radio>
                            <el-radio v-model="userForm.sex" label="2"></el-radio>
                        </el-form-item>
                        <el-form-item label="" prop="phoneNumber">
                            <el-input v-model="userForm.phoneNumber"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false"> </el-button>
                                <el-button type="primary" @click="update"> </el-button>
                            </span>
                </el-dialog>
            </el-main>

        </el-container>
    </div>
</template>

<script>
    import * as CommonUrls from '../../api/commonUrls'
    import * as userDQL from '../../api/user'
    import {request} from "../../utils/request";

    export default {
        name: "user",
        data() {
            return {
                input: '',
                userName: '',
                /**/
                userForm: {
                    id: '',
                    userName: '',
                    sex: '',
                    password: '',
                    phoneNumber: '',
                },
                /**/
                updateUser: {
                    id: '',
                    userName: '',
                    sex: '',
                    password: '',
                    phoneNumber: '',
                },
                /**/
                dialogVisible: false,

                tableData: [],
                limit: 100, // todo ,
                offset: 0, //todo 
            }
        },
        methods: {
            handleSizeChange(val) {
                console.log(` ${val} `);
                this.limit = val;
            },
            // val
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                this.offset = val;
            },
            /**/
            openGet(id) {
                if (id) {
                    this.dialogVisible = true;
                    this.userForm.id = id;
                    userDQL.selectUserByID({
                        id: this.userForm.id
                    }).then(res => {
                        console.log(res);
                        this.tableData = res;
                    }).catch(function (err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    })
                } else {
                    this.$message("");
                }
            },
            closeGet() {
                this.flag = !this.flag;
                this.$message("");
            },
            /**/
            update() {
                //MD5
                let password = this.$md5(this.userForm.password);
                fetch("/front/api/user/update", {
                    method: "post",
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({
                        id: this.userForm.id,
                        userName: this.userForm.userName,
                        sex: this.userForm.sex,
                        password: password,
                        phoneNumber: this.userForm.phoneNumber
                    })
                })
                    .then(res => {
                        if (res.data.code === 200) {
                            this.$message("");
                            this.dialogVisible = true;
                            this.reload();
                        }
                    })
                    .catch(function (err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    });
            },
            /**/
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            toDelete(id) {
                console.log(id);
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let params = {id: id};
                    request({
                        url: 'api/user/deleteById',
                        method: 'post',
                        data: this.qsParam(params),
                        header: {'Content-Type': 'application/x-www-form-urlencoded'}
                    }).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("");
                                /**/
                                this.GetTableData();
                            } else {
                                this.$message.error("");
                            }
                        }
                    )
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            /**/
            GetTableData() {
                CommonUrls.getAllUsers({
                    limit: this.limit,
                    offset: this.offset,
                }, 'get').then(res => {
                    return res.json();
                }).then(res => {
                    // if (res.code === 200) {
                    this.tableData = res;
                    // } else {
                    //     console.log("")
                    // }
                }).catch(err => {
                    console.log(err)
                })
            }
        },
        mounted() {
            this.GetTableData();
        }
    }
</script>

<style scoped>
    .getInfo1 {
        position: absolute;
        z-index: 1001;
        width: 400px;
        height: 490px;
        left: 0;
        top: -50px;
        bottom: 0;
        right: 0;
        margin: auto;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
    }
</style>