<template>
    <div class="purchase">
        <!---->
        <div class="steps">
            <el-steps :space="400" :active="1" finish-status="success">
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
        </div>
        <!--todo -->
        <div>
            <area-select :level="2" type="text" v-model="selected" :data="pcaa"></area-select>
            {{selected}}}
        </div>
        <span>-></span>
        <el-date-picker
                v-model="date"
                type="date"
                :editable="false"
                placeholder=""
                value-format="yyyy-MM-dd"
                @change="handleDateChange"
                :picker-options="pickerOptions0">
        </el-date-picker>
        <div class="al-p-10px" v-if="schedule != null">
            <span v-for="(item, index) in schedule" :key="index">
                <span
                        class="al-p-10px al-m-left-20px"
                        @click="getSeats(item.id)">
                    <span v-if="activeTimetable" class="al-text-color-light-red">
                        {{item.schedule}}
                    </span>
                    <span v-else>
                        {{item.schedule}}
                    </span>
                </span>
            </span>
        </div>
        <!--        <span>{{schedule.room}}</span>-->
        <el-row :gutter="10">
            <el-col :span="16">
                <div class="seat-wrapper">
                    <div class="illustration">
                        <div class="illustration-img-wrapper unselected-seat">
                        </div>
                        <span class="illustration-text"></span>
                        <div class="illustration-img-wrapper selected-seat">
                        </div>
                        <span class="illustration-text"></span>
                        <div class="illustration-img-wrapper bought-seat">
                        </div>
                        <span class="illustration-text"></span>
                    </div>
                    <div class="screen">
                        <!--todo cinema-->
                        3
                    </div>
                    <div class="screen-center">
                        
                        <!--                        <div class"mid-line"=></div>-->
                    </div>
                    <!---->
                    <div class="seatChoose">
                        <!---->
                        <span v-for="(li,index) in 200"
                              :class="[isSell[index]?'bg-sited':isClick[index]?'bg-beChosen':'bg-sit']" :key="index"
                              @click="isSell[index]?cantSelect():clickSeats(index)">
                        </span>
                    </div>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="side">
                    <div class="movieInfoBox">
                        <el-row :gutter="10">
                            <el-col :span="12">
                                <img :src="movieInfo.poster" alt="" class="posters">
                            </el-col>
                            <el-col :span="12">
                                <span>
                                    {{movieInfo.movie.movieName}}
                                </span><br>
                                <span>{{movieInfo.movieLength}}</span>
                            </el-col>
                        </el-row>
                        <span></span><br>
                        <span></span><br>
                        <span><span style="color: red">  321 14:00</span>
                        </span><br>
                        <span>
                        </span><br>
                        <span><span style="color: #39ac6a">74/</span>
                        </span><br>
                        <span></span><br>

                        <div>
                            <el-button type="success" @click="confirmSeats"></el-button>
                            <el-button type="danger" @click="confirmSeats"></el-button>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>

        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <span>{{this.orderID}}</span><br>
            <span></span><br>
            <span></span><br>
            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false"> </el-button>
                                <el-button type="primary" @click="toPay"> </el-button>
                            </span>
        </el-dialog>
    </div>

</template>

<script>
    import {AllScheduleByDate} from '../../api/schedule'
    import {querySeatById} from '../../api/seat'
    import {request} from "../../utils/request";
    import {pay} from "../../api/commonUrls";
    /**/
    import {AreaCascader} from "vue-area-linkage"
    import {pca, pcaa} from 'area-data';

    export default {
        data() {
            return {
                /**/
                selected: [], //type
                //selected[0]selected[1]selected[2]
                pca: pca,
                pcaa: pcaa,

                chooseSeats: [], //
                isClick: [], //
                isSell: [], //
                //
                /**/
                dialogVisible: false,
                id: this.$route.params.mid,
                radio: '',
                date: '', //
                tableData: [],
                /**/
                orderID: null,
                order: {
                    /**/
                    orderId: '',
                    /**/
                    seats: '',
                    /*cinema*/
                    cinemaId: '',
                    /**/
                    /*todo   */
                    date: '',
                    runTime: '',
                    endTime: '',
                },
                schedule: {}, //scheduleschedule
                pickerOptions0: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 8.64e7;
                    }
                },

                timetable: [],
                chooseDate: null,
                activeTimetable: false,
                /**/
                movieInfo: {},
                cinemaInfo: {}

            }

        },
        methods: {
            //
            choseSeats(index) {
                if (this.chooseSeats.length !== 0) {
                    let index2 = this.chooseSeats.indexOf(index);
                    if (index2 === -1) {
                        this.chooseSeats.push(index);
                        this.$set(this.isClick, index, true);
                    } else {
                        this.$set(this.isClick, index, false);
                        this.chooseSeats.splice(index2, 1)
                    }
                } else {
                    this.chooseSeats.push(index);
                    this.$set(this.isClick, index, true);
                }
            },
            //
            clickSeats(index) {
                if (this.scheduleId != null && this.chooseDate != null) {
                    this.choseSeats(index);
                    console.log(this.chooseSeats)
                } else {
                    this.$message.warning("");
                }

            },
            cantSelect() {
                this.$message.warning("");
                return false;
            },
            toPay() {
                /*orderID*/
                let params = {};
                pay({
                    orderID: '12312312312312',
                    total: '111',
                    movieName: '',
                    discription: ''
                }, 'post').then(res => {
                    return res.json();
                }).then(res => {
                    console.log(res);
                    document.querySelector('body').innerHTML = res;
                    //div
                    const div = document.createElement('div');
                    //formdiv
                    div.innerHTML = res;
                    document.body.appendChild(div);
                    document.forms[0].submit()
                }).catch(err => {
                    console.log(err);
                })
            },
            //
            confirmSeats() {
                console.log(this.chooseSeats.toString());
                /*movieID*/

                let params = {
                    rows: this.chooseSeats.toString(), //
                    /*todo scheduleID*/
                    scheduleId: this.scheduleId,
                    /**/
                    order: {
                        date: this.chooseDate,
                        runTime: this.schedule.schedule,
                        /*todo */
                        endTime: '',
                        /**/
                        total: '',
                        /**/
                        /*movie*/
                        movieName: this.movieInfo.movie.movieName,
                        /*cinema*/
                        address: '',
                        // order: ''//address,status,runtimeschuduleschedule,seatsrows
                    },

                };
                console.log(params);
                request({
                    url: 'api/seat/add',
                    method: 'post',
                    // data: this.qsParam(params),
                    data: this.qsParam(params),
                    header: {'Content-Type': 'application/x-www-form-urlencoded'}
                }).then(res => {
                    // console.log(res)
                    if (res.data.code === 200) {
                        this.$message.success("");
                        /**/
                        this.orderID = res.data.msg.orderId;
                        console.log(this.orderID);
                        this.dialogVisible = true;
                    } else if (res.data.code === 404) {
                        this.$message.error("");
                    } else {
                        console.log(res.data.code);
                        this.$message.error("");
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            //
            //
            getSchedule(date) {
                AllScheduleByDate({
                    dateStr: date,
                    movie_id: this.id,
                }, 'get').then(res => {
                    return res.json()
                }).then(res => {
                    this.schedule = res.data;
                    console.log("scheduleInfo: ");
                    console.log(this.schedule);

                    //
                    this.getCinemaInfo(this.schedule[0].cinema_id);

                    for (let item of res.data) {
                        // console.log(item.date);
                        this.getTimetable(item.schedule);
                        this.timetable.push(item.schedule);
                        //this.getSeats(item.id);
                    }
                    //this.getSeats();
                }).catch(err => {
                    console.log(err);
                })
            },

            //
            getTimetable(schedule) {
                console.log(schedule);
                // request({
                //     url: 'api/schedule/queryByDate',
                // }).then(res => {
                //     console.log(res);
                // }).catch(err => {
                //     console.log(err);
                // });
            },


            //
            getSeats(scheduleId) {
                this.activeTimetable = true;
                console.log("activeTimetable: " + this.activeTimetable);
                console.log(scheduleId);
                this.scheduleId = scheduleId;
                querySeatById({scheduleId: scheduleId}, 'get').then(res => {
                    return res.json()
                }).then(res => {
                    console.log(res.data);
                    for (let i of res.data) {
                        this.$set(this.isSell, i.row, true);
                    }

                }).catch(err => {
                    console.log(err);
                    this.$message.error('');
                })
            },

            handleDateChange(date) {
                // this.date = date;
                console.log(date);
                this.chooseDate = date;
                // this.$message.success(date);
                this.getSchedule(date);

            },
            /**/
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },


            /**
             * 
             * @param id
             */
            getMovieInfoById(id) {
                request({
                    url: 'api/movieInfo/selectOne?id=' + id,
                }).then(res => {
                    console.log(res);
                    this.movieInfo = res.data;
                }).catch(err => {
                    console.log(err);
                });
            },

            /**
             * 
             * @param cinemaId
             */
            getCinemaInfo(cinemaId) {
                request({
                    url: 'api/cinema/selectOne?id=' + cinemaId,
                }).then(res => {
                    console.log(res);
                    this.cinemaInfo = res.data;
                    console.log(this.cinema);
                }).catch(err => {
                    console.log(err);
                });
            }
        },
        // 
        filters: {

            // 
            time(data) {
                let date = new Date(data);
                let month = date.getMonth() + 1;
                let day = date.getDate();
                let hours = date.getHours() > 9 ? date.getHours() : ("0" + date.getHours());
                let minutes = date.getMinutes() > 9 ? date.getMinutes() : ("0" + date.getMinutes());
                return month + "" + day + "  " + hours + ":" + minutes;
            },


            // ,
            price(data) {
                let price = parseFloat(data);
                return price.toFixed(2);
            }
        },

        mounted() {
            // this.getSeats();
            this.getMovieInfoById(this.$route.params.mid);

        }

    }

</script>

<style scoped>
    html,
    body {
        margin: 0;
        padding: 0;
        height: 100%;
    }

    .payChooseWrapper {
        justify-content: center;
        align-items: center;
    }

    .el-header {
        background-color: #ffffff;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-main {
        background-color: #e8f0fa;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .sit {
        height: 35px;
        width: 35px;
        min-height: 10px;
        min-width: 10px;
        margin: 4px 4px 4px 4px;
        background-size: cover;
        /* border: 1px solid black; */
    }

    /*  */
    .bg-sit {
        width: 20px;
        height: 20px;
        background-position: center center;
        background-size: cover;
        list-style: none;
        background-image: url("../../assets/img/bg-unsit.png");
    }

    .bg-sit:hover {
        cursor: pointer;
    }

    /*  */
    .bg-sited {
        /* background-color: red; */
        width: 20px;
        height: 20px;
        background-position: center center;
        background-size: cover;
        background-image: url('../../assets/img/bg-sited.png');
    }

    /*  */
    .bg-nosit {
        /* border: 1px solid white; */
    }

    /*  */
    .bg-beChosen {
        cursor: pointer;
        width: 20px;
        height: 20px;
        background-position: center center;
        background-size: cover;
        background-image: url('../../assets/img/bg-bechosen.png');
    }

    /*  */
    .select-sit {
        border: 2px orange solid;
        border-radius: 10px;
        margin-left: 10px;
        padding: 4px 10px 4px 10px;
        background-color: rgba(255, 0, 0, 0.2);
    }

    .row {
        /*  */
        clear: both;
    }

    /*  */
    .btn {
        border-radius: 10px;
        width: 200px;
        height: 50px;
        /* padding: 4px 10px 4px 10px; */
        background-color: rgba(255, 0, 0, 0.8);
        font-size: 25px;
        color: white;
        font-family: '';
    }

    /*  */
    .win-right {
        padding-left: 2%;
        padding-top: 2%;
        width: 27%;
        background-color: rgba(112, 112, 112, 0.2);
        border: 1px white solid;
        height: 100%;
    }

    /*  */
    .box {
        width: 90%;
        margin-left: 5%;
        height: 64%;
    }

    /*  */
    .film-img {
        width: 80%;
    }

    /*  */
    .film-title {
        font-family: "";
        font-weight: 900;
        font-size: 25px;
    }


    .wrapper {
        height: 100%;
        padding: 40px;
        box-sizing: border-box;
    }

    .cinema-wrapper {
        height: 100%;
    }

    .title {
        text-align: center;
    }

    .seat-wrapper {
        height: 700px;
        width: 1000px;
        border: 1px dotted #c5c5c5;
        margin: 0 auto;
        position: relative;
        overflow: hidden;
    }

    .screen {
        margin: 0 auto;
        height: 30px;
        width: 300px;
        background-color: #e3e3e3;
        border-radius: 0 0 30px 30px;
        color: #585858;
        line-height: 30px;
        text-align: center;
    }

    .inner-seat-wrapper {
        position: absolute;
        top: 120px;
        bottom: 0;
        width: 100%;
        box-sizing: border-box;
    }

    .seat {
        float: left;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .inner-seat {
        width: 80%;
        height: 80%;
        cursor: pointer;
    }

    .selected-seat {
        background: url('../../assets/img/bg-bechosen.png') center center no-repeat;
        background-size: 100% 100%;
    }

    .unselected-seat {
        background: url('../../assets/img/bg-unsit.png') center center no-repeat;
        background-size: 100% 100%;
    }

    .bought-seat {
        background: url('../../assets/img/bg-sited.png') center center no-repeat;
        background-size: 100% 100%;
    }

    .screen-center {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        padding: 5px;
        font-size: 13px;
        border-radius: 5px;
        top: 50px;
        background-color: #f6f6f6;
        color: #636363;
        border: 1px solid #b1b1b1;
    }

    .mid-line {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 100%;
        width: 1px;
        height: 800px;
        border-left: 1px dashed #919191;
    }

    .illustration {
        position: absolute;
        left: 0;
        top: 0;
        height: 35px;
        width: 300px;
    }

    .illustration-img-wrapper {
        width: 25px;
        height: 25px;
        position: relative;
        top: 50%;
        display: inline-block;
        transform: translateY(-50%);
        margin-left: 10px;
    }

    .illustration-text {
        display: inline-block;
        height: 100%;
        line-height: 35px;
        font-size: 14px;
        position: relative;
        top: -2px;
    }

    .side {
        width: 450px;
        height: 750px;
        background-color: #ffffff;
        color: #999999;
    }

    .movieInfoBox {
        margin: 20px 20px 0 20px;
        width: 350px;
        height: 162px;
    }

    .steps {
        margin: 0 auto;
        width: 1200px;
        height: 100%;
    }

    .posters {
        height: 158px;
        width: 115px;
    }

    .seatChoose {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding: 60px 20px;
    }

    .seatChoose span {
        width: 35px;
        margin: 5px 5px;
        height: 35px;
    }
</style>
