import axios from 'axios';

// let ip = "localhost";
// let ip = "localhost";
//
let ip = "47.116.19.145";
let port = "8081";

let prefix = "http://" + ip + ':' + port + "/";

export function request(config) {
    const instance = axios.create({
        baseURL: prefix,
        timeout: 100000
    });

    return instance(config);

}