<template>
    <div class="cinema">
        <el-main>
            <div>
                <el-form
                        ref="searchForm"
                        :inline="true"
                        :model="searchMap"
                        style="margin-top: 20px;margin-left: 0px">
                    <el-form-item prop="cinemaForm.address">
                        <el-input>
                            <template slot="prepend"></template>
                        </el-input>
                    </el-form-item>
                    <el-button icon="el-icon-search"></el-button>
                    <el-button type="primary" plain @click="dialogVisible = true"></el-button>
                </el-form>
            </div>
            <el-table
                    :data="tableData"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="id"
                        width="100px">
                </el-table-column>
                <el-table-column
                        prop="cinemaName"
                        label=""
                        width="100px">
                </el-table-column>
                <el-table-column
                        prop="room"
                        label=""
                        width="100px">
                </el-table-column>
                <el-table-column
                        prop="seats"
                        label=""
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="address"
                        label=""
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="telephone"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="modifiedTime"
                        label=""
                >
                </el-table-column>

                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <el-button type="success" @click="toUpdate(scope.row.id)"></el-button>
                        <el-button type="danger" @click="toDelete(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--todo -->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,20,30]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
            ></el-pagination>
        </el-main>
        <!---->
        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="35%"
                :before-close="handleClose"
                center="true"
                destroy-on-close="true">
            <el-form ref="form" :model="tableData" label-width="80px">
                <el-form-item label="" prop="cinemaName">
                    <el-input type="text" v-model="cinemaForm.cinemaName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="seats">
                    <el-input v-model="cinemaForm.seats"></el-input>
                </el-form-item>
                <el-form-item label="" prop="room">
                    <el-input v-model="cinemaForm.room" placeholder="3D"></el-input>
                </el-form-item>
                <el-form-item label="" prop="address">
                    <el-input v-model="cinemaForm.address" type="textarea" placeholder="1"></el-input>
                </el-form-item>
                <el-form-item label="" prop="telephone">
                    <el-input v-model="cinemaForm.telephone"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false"> </el-button>
                                <el-button type="primary" @click="addCinema"> </el-button>
                            </span>
        </el-dialog>
        <!---->
        <el-dialog
                title=""
                :visible.sync="dialogVisible2"
                width="35%"
                :before-close="handleClose"
                center="true"
                lock-scroll="false">
            <el-form ref="form" :model="tableData" label-width="80px">
                <el-form-item label="" prop="cinemaName">
                    <el-input type="text" v-model="cinemaForm.cinemaName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="seats">
                    <el-input v-model="cinemaForm.seats"></el-input>
                </el-form-item>
                <el-form-item label="" prop="room">
                    <el-input v-model="cinemaForm.room" placeholder="3D"></el-input>
                </el-form-item>
                <el-form-item label="" prop="address">
                    <el-input v-model="cinemaForm.address" type="textarea" placeholder="1"></el-input>
                </el-form-item>
                <el-form-item label="" prop="telephone">
                    <el-input v-model="cinemaForm.telephone"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible2 = false"> </el-button>
                                <el-button type="primary" @click="toUpdate(id)"> </el-button>
                            </span>
        </el-dialog>

    </div>
</template>

<script>
    import * as cinema from "../../api/cinema"
    import {request} from "../../utils/request";

    export default {
        name: "cinema",
        data() {
            return {
                input: '',
                /**/
                dialogVisible: false,
                /**/
                dialogVisible2: false,
                /**/
                cinemaForm: {
                    id: '',
                    cinemaName: '',
                    room: '',
                    address: '',
                    telephone: '',
                    seats: '',
                    createTime: '',
                    modifiedTime: ''
                },
                tableData: [],
                limit: '', // todo 
                offset: '',
            }
        },
        methods: {
            /**/
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            GetTableData() {
                cinema.getAllCinema({
                    limit: this.limit,
                    offset: this.offset,
                }, 'get').then(res => {
                    return res.json();
                }).then(res => {
                    // if (res.code === 200) {
                    this.tableData = res;
                    // } else {
                    //     console.log("")
                    // }
                }).catch(err => {
                    console.log(err)
                })
            },
            addCinema() {
                if (this.cinemaForm.cinemaName) {
                    let params = {
                        cinemaName: this.cinemaForm.cinemaName,
                        room: this.cinemaForm.room,
                        address: this.cinemaForm.address,
                        telephone: this.cinemaForm.telephone,
                        seats: this.cinemaForm.seats,
                    };
                    console.log("------------------")
                    console.log(params);
                    request({
                        url: 'api/cinema/add',
                        method: 'post',
                        /**/
                        data: JSON.stringify(params),
                        headers: {'Content-Type': 'application/json'}
                    }).then(res => {
                            if (res.data.code === 200) {
                                this.dialogVisible = false;
                                this.$message.success("");
                                /**/
                                // this.reload();
                                this.GetTableData();
                            } else {
                                this.$message.error("");
                            }
                        }
                    ).catch(function (err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    });
                } else {
                    this.$message('')
                }
            },
            toDelete(id) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let params = {id: id};
                    request({
                        url: 'api/cinema/deleteById',
                        method: 'post',
                        data: this.qsParam(params),
                        header: {'Content-Type': 'application/x-www-form-urlencoded'}
                    }).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("");
                                /**/
                                this.GetTableData();
                            } else {
                                this.$message.error("");
                            }
                        }
                    )
                }).catch(() => {
                    this.$message.info("");
                });
            },
            toUpdate(id) {
                if (id) {
                    this.dialogVisible2 = true;
                    this.userForm.id = id;
                    let params = {
                        cinemaName: this.cinemaForm.cinemaName,
                        room: this.cinemaForm.room,
                        address: this.cinemaForm.address,
                        telephone: this.cinemaForm.telephone,
                        seats: this.cinemaForm.seats,
                    };
                    request({
                        url: 'api/cinema/update',
                        method: 'post',
                        data: this.qsParam(params),
                        header: {'Content-Type': 'application/x-www-form-urlencoded'}
                    }).then(res => {
                            if (res.data.code === 200) {
                                this.dialogVisible2 = false;
                                this.$message.success("");
                                /**/
                                this.GetTableData();
                            } else {
                                this.$message.error("");
                            }
                        }
                    ).catch(function (err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    });
                } else {
                    this.$message.info("");
                }
            }
        },
        created() {

        },
        mounted() {
            this.GetTableData();
        }
    }
</script>

<style scoped>

</style>